import Game from "@/Game"
import util from "@/util"

export default class Ability {
  private name: string = ""
  private cost: { value: number; resource: string } // resource: mana, bonus
  private fun: (game: Game) => any
  private available: (game: Game) => boolean
  public secret: boolean = false // True if this ability should be invisible when not available

  constructor(name: string, cost: { value: number; resource: string },
    fun: (game: Game) => any,
    isAvailable: (game: Game) => boolean = game => true) {

    this.name = name
    this.cost = cost
    this.fun = fun
    this.available = isAvailable
  }

  activate(game: Game) {
    if (this.isAvailable(game)) {
      if (this.cost.resource == "mana") game.player.useMana(this.cost.value)
      if (this.cost.resource == "bonus") game.bonusRolls.addValue(-this.cost.value)

      this.fun(game)
    }
  }

  isAvailable(game: Game): boolean {
    return this.available(game) && (
      (this.cost.resource == "mana" && this.cost.value <= game.player.getMana()) ||
      (this.cost.resource == "bonus" && game.bonusRolls.value - this.cost.value <= game.bonusRolls.max)
    )
  }

  isVisible(game: Game): boolean {
    return this.isAvailable(game) || (!this.isAvailable(game) && !this.secret)
  }

  get title(): string {
    return `${this.name}`
  }

  get subtitle(): string {
    if (this.cost.value > 0) return `Pay ${this.cost.value} ${this.cost.resource}`
    else if (this.cost.value < 0) return `Gain ${-this.cost.value} ${this.cost.resource}`
    return ``
  }

  static buyMana(): Ability {
    let a = new Ability("+2 mana", { value: -1, resource: "bonus" },
      game => {
        game.player.addMana(2)
      },
      game => {
        return game.bonusRolls.fraction < 1 && !game.allowDiceSale()
      }
    )
    a.secret = true
    return a
  }

  static buyDice(): Ability {
    return new Ability("Buy dice", { value: 2, resource: "mana" },
      game => {
        game.diceCount += 1
      },
      game => {
        return game.allowDiceBuy()
      }
    )
  }

  static sellDice(): Ability {
    let a = new Ability("Sell dice", { value: -2, resource: "mana" },
      game => {
        game.diceCount -= 1
      },
      game => {
        return game.allowDiceSale()
      }
    )
    a.secret = true
    return a
  }

  static pickAnyCard(cardPickFunction: (diceRolls: number[]) => any, deckSize: number, fixedCost: number = -1): Ability {
    let cost = 5 + (deckSize - 5)
    cost = util.clamp(cost, 5, 10)
    if (fixedCost >= 0) cost = fixedCost

    let a = new Ability("Pick Any",
      { value: cost, resource: "mana" },
      game => {
        let highlights = []
        for (let i = 0; i < deckSize; i++) {
          highlights.push(i + 1)
        }
        cardPickFunction(highlights)
      })
    return a
  }

}
