import GameStage from "@/stages/GameStage"
import Game from "@/Game"
import TextStage from "@/stages/TextStage"
import Dice from "@/Dice"
import Card from "@/cards/Card"
import Deck from "@/cards/Deck"
import PickStage from "@/stages/PickStage"
import HighlightStage from "@/stages/HighlightStage"
import Ability from "@/Ability"

export default class IntensityStage extends GameStage {
  private pickStage!: PickStage
  private parentCard!: Card
  private deck: Deck = new Deck([])
  private done: boolean = false
  private abilities: Ability[] = []

  constructor(game: Game) {
    super(game, "intensity")
  }

  onStart() {
    this.pickStage = this.args.pickStage
    this.parentCard = this.args.parentCard
    this._allowDiceBuy = (this.args.allowDiceBuy != undefined) ? this.args.allowDiceBuy : true
    //this.text = `demon: Ah, you got ${this.parentCard.name}.`
    this.game.diceCount = 1
    if (this.parentCard.levels >= 4) {
      this.game.diceCount += 1
    }
    if (this.parentCard.type == 'numeric') {
      this.createMessage(this.game.dialogue['intensityStage'])
    } else {
      this.createMessage(this.game.dialogue['intensityStageSelection'])
      if (this.parentCard.levels >= 5) {
        this.game.diceCount += 1
        this.createMessage(`demon: Since this card is mostly just personal preference anyways, I'll let you have an extra dice for free.`)
      }
    }
    this.done = false

    this.deck = new Deck(this.parentCard.subcards).sortByLevel()
    this.game.drawnCards = this.deck
    this.game.diceSize = this.deck.size()

    this.abilities = [
      Ability.buyMana(),
      Ability.sellDice(),
      Ability.buyDice(),
      Ability.pickAnyCard((values: number[]) => this.pickCard(values), this.deck.size())
    ]
    this.game.abilities = this.abilities
  }

  onDiceRoll(values: number[]) {
    this.pickCard(values)
  }

  pickCard(values: number[]) {
    if (this.done) return
    this.done = true
    this.game.lockDice(true)

    let intensities: Deck = this.deck.pick(values)
    this.nextStage(new HighlightStage(this.game), { pickStage: this.pickStage, availableCards: this.deck, highlights: values })
  }
}
