import Deck from "@/cards/Deck"
import FilterSettings from "@/FilterSettings"
import FilterSetting from "@/FilterSetting"

export default class Requirement {
  mode: string = "or" // or, and
  private args: string[] = []
  private operator: string = ">"
  private intensity: number = 0
  private fun: (deck: Deck, args: string[]) => number = Requirement.intensitySum

  constructor(mode: string, args: string[], operator: string, intensity: number, fun: (deck: Deck, args: string[]) => number = Requirement.intensitySum) {
    this.mode = mode
    this.args = args
    this.operator = operator
    this.intensity = intensity
    this.fun = fun
  }

  static functions(name: string): (deck: Deck, args: string[]) => number {
    let availableFunctions: { [key: string]: (deck: Deck, args: string[]) => number } = {
      sum: Requirement.intensitySum
    }
    return availableFunctions[name]
  }

  isFulfilled(deck: Deck): boolean {
    if (this.operator == "<") {
      return this.intensityScore(deck) < this.intensity
    } else if (this.operator == "<=") {
      return this.intensityScore(deck) <= this.intensity
    } else if (this.operator == "==") {
      return this.intensityScore(deck) == this.intensity
    } else if (this.operator == ">=") {
      return this.intensityScore(deck) >= this.intensity
    } else if (this.operator == ">") {
      return this.intensityScore(deck) > this.intensity
    }
    console.error(`Requirement operator "${this.operator}" was not recognized.`)
    return false
  }

  intensityScore(deck: Deck): number {
    return this.fun(deck, this.args)
  }

  private static intensitySum(deck: Deck, idsOrTags: string[]): number {
    let sum = 0
    for (let idOrTag of idsOrTags) {
      if (Deck.referenceDeck().hasCard(idOrTag)) { // Id may be for card
        let card = deck.getCardById(idOrTag)

        if (card == undefined) {
          // Id may be for a parent card, in that case, count the highest available subcard
          card = deck.pack().getCardById(idOrTag)
          if (card != undefined) sum += card.subcards[card.subcards.length - 1].level

        } else {
          sum += Math.max(card.level, 1)
        }
      } else if (Deck.referenceDeck().onlyTag(idOrTag).size() > 0) { // Id may be a tag or family name
        for (let card of deck.onlyTag(idOrTag).cards) {
          sum += Math.max(card.level, 1)
          //console.log(`Adding Card ${card.fullTitle} level ${Math.max(card.level, 1)}`)
        }
      } else {
        throw new Error(`Bad requirement "${this.toString()}", "${idOrTag}" is neither an id, tag or family.`)
      }
    }
    return sum
  }

  toString(): string {
    return `${this.mode.toUpperCase()} [${this.args}] [${this.operator}] [${this.intensity}]`
  }

}
