import Deck from "@/cards/Deck"
import ValueRange from "@/effects/ValueRange"
import Ability from "@/Ability"

export default class Player {
  public hand: Deck = new Deck()
  private mana: number = 0
  public abilities: Ability[] = []

  useMana(cost: number): boolean {
    if (cost <= this.mana) {
      this.mana -= cost
      return true
    }
    return false
  }

  addMana(amount: number) {
    this.mana += amount
  }

  getMana(): number {
    return this.mana
  }
}
