<template>
  <div class="milkfarm">
    <CardHandWindow :menu="menu" :deck="game.drawnCards" class="drawn-cards">
      <div class="hand-labels">
        <div class="hand-label">{{ game.roundString }}</div>
        <div class="hand-label mana-label">
          Bonus rolls: {{ game.state.bonusRolls.value }} / {{ game.state.bonusRolls.max }}
        </div>
        <div class="hand-label mana-label">
          Mana: {{ game.player.getMana() }}
        </div>
        <div class="hand-label">
          {{ game.pickedString }}
        </div>
      </div>
    </CardHandWindow>
    <CardHandWindow :menu="menu" :deck="handDeck"
                    class="picked-cards">
      <div class="hand-labels">
        <TextButton class="hand-button" @click="showActivatedCards = !showActivatedCards">
          <template v-if="showActivatedCards">Show All: On</template>
          <template v-else>Show All: Off</template>
        </TextButton>
      </div>
    </CardHandWindow>

    <DialogueCard :menu="menu"></DialogueCard>

    <div class="dices">
      <template v-for="die in game.state.dice" v-bind:key="die">
        <DiceView :dice="die"></DiceView>
      </template>
      <!--<div class="dice-view add-dice" v-if="game.allowDiceBuy()" @click="game.buyDice()">
        <p>+</p>
      </div>-->
      <!--<div class="dice-view sell-dice" v-if="game.allowDiceSale()" @click="game.sellDice()">
        <p>Sell</p>
      </div>-->
    </div>

    <div class="abilities">
      <template v-for="ability in game.abilities" v-bind:key="ability">
        <TextButton v-if="ability.isVisible(game)" class="ability" :disabled="!ability.isAvailable(game)" @click="ability.activate(game)">
          <p class="ability-title">{{ ability.title }}</p>
          <p class="ability-subtitle">{{ ability.subtitle }}</p>
        </TextButton>
      </template>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Game from '@/Game'
import TextButton from '@/components/TextButton.vue'
import PlayingCard from "@/components/PlayingCard.vue"
import Card from '@/cards/Card'
import DiceView from "@/components/DiceView.vue"
import Menu from "@/Menu"
import DialogueStatusCard from "@/components/DialogueStatusCard.vue"
import CardHandWindow from "@/components/CardHandWindow.vue"
import Deck from "@/cards/Deck"
import DialogueCard from "@/components/DialogueCard.vue"

@Options({
  components: {
    TextButton, PlayingCard, DiceView, DialogueCard, DialogueStatusCard, CardHandWindow
  },
  props: {
    menu: Object
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    toggleScale(card: Card) {
      card.scale = !card.scale
      if (this.scaledCard == card) {
        this.scaledCard.scale = false
        this.scaledCard = new Card({})
      } else {
        this.scaledCard.scale = false
      }
      if (card.scale) this.scaledCard = card
    }
  },
  computed: {
    game: function (): Game {
      return this.menu.game
    },
    handDeck: function (): Deck {
      if (this.showActivatedCards) return this.game.player.hand.copy().addDeck(this.game.pickedCards)
      return this.game.pickedCards
    }
  }
})
export default class GameScreen extends Vue {
  menu!: Menu
  scaledCard: Card = new Card({})
  showActivatedCards: boolean = false
}
</script>

