import GameStage from "@/stages/GameStage"
import Game from "@/Game"
import TextStage from "@/stages/TextStage"
import Dice from "@/Dice"
import Card from "@/cards/Card"
import Deck from "@/cards/Deck"
import PickStage from "@/stages/PickStage"
import IntensityStage from "@/stages/IntensityStage"

export default class HighlightStage extends GameStage {
  private pickStage!: PickStage
  private availableCards: Deck = new Deck([])
  private highlights: number[] = []
  private highlightedCards: Deck = new Deck([])
  private done: boolean = false

  constructor(game: Game) {
    super(game, "highlight")
  }

  onStart() {
    this.pickStage = this.args.pickStage
    this.availableCards = this.args.availableCards
    this.highlights = this.args.highlights

    this.highlightedCards = this.availableCards.pick(this.highlights)
    this._allowDiceBuy = (this.args.allowDiceBuy != undefined) ? this.args.allowDiceBuy : true
    if (this.availableCards.size() == 1) {
      this.createMessage(`You got **${this.availableCards.cards[0].fullTitle}**. Click on it to add it to your hand.`)
    } else {
      this.createMessage(`Since you rolled multiple dice you may choose any of the highlighted cards.`)
    }

    for (let highlight of this.highlights) {
      let card = this.availableCards.pick([highlight]).cards[0]
      card.highlight += 1
    }

    this.done = false
  }

  onCardClick(card: Card) {
    if (card.highlight > 0) {
      this.game.player.addMana(card.highlight - 1)

      for (let c of this.availableCards.cards) {
        c.highlight = 0
        c.scale = false
      }
      if (card.isParent) {
        this.pickStage.deck.removeCard(card)
        this.nextStage(new IntensityStage(this.game), { pickStage: this.pickStage, parentCard: card, allowDiceBuy: this._allowDiceBuy })
      } else {
        this.pickStage.deck.removeCard(card)
        this.game.pickCard(card)
        this.resumeStage(this.pickStage)
      }
    }
  }
}
